/* App.css */
body {
  margin: 0;
  font-family: 'NanumSquare', sans-serif;
  background-color: #f8f9fa;
  color: #333;
}

header,
footer {
  background-color: #0056b3;
  color: white;
  padding: 20px;
  text-align: center;
}

h1,
h2 {
  margin: 10px 0;
  font-weight: bold;
}

button {
  background-color: #0056b3;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
}

button:hover {
  background-color: #003f7f;
}

section {
  padding: 40px;
  margin: 20px auto;
  max-width: 800px;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

form label {
  display: block;
  margin-bottom: 10px;
  font-weight: bold;
}

form input,
form select {
  width: 100%;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
  font-size: 14px;
}

ul {
  list-style: none;
  padding: 0;
}

ul li {
  background-color: #e9f4ff;
  margin: 10px 0;
  padding: 10px;
  border-radius: 5px;
}